import { MimeTypeGroup } from "~/functions/attachments";
export default {
  name: "Attachment",
  props: {
    attachmentPromise: {
      type: Promise,
      default: undefined,
    },
  },
  data() {
    return {
      attachment: undefined,
      MimeTypeGroup,
    };
  },
  async fetch() {
    this.attachment = await this.attachmentPromise;
  },
};
